#respond textarea {
  min-height: 160px;
  padding: 12px;
}
#respond .trumbowyg-box,
#respond .trumbowyg-editor {
  min-height: 160px;
}
#respond .trumbowyg-editor {
  background: #fff;
}
#respond .trumbowyg-editor {
  word-wrap: break-word;
}
#respond .trumbowyg-editor ul,
#respond .trumbowyg-editor ol,
#respond .trumbowyg-editor li,
#respond .trumbowyg-editor p,
#respond .trumbowyg-editor strike,
#respond .trumbowyg-editor em {
  font-size: 14px;
}
#respond .trumbowyg-editor ul,
#respond .trumbowyg-editor ol {
  padding: 0 0 0 18px;
  margin: 0;
}
#respond .trumbowyg-editor.empty {
  box-shadow: inset 0 0 10px rgba(199, 64, 73, 0.4);
}
